import React, { Component } from 'react';
// import styled from 'styled-components';
import PropTypes from 'prop-types';
import Alert from '@material-ui/lab/Alert';
import AlertTitle from '@material-ui/lab/AlertTitle';
import IconButton from '@material-ui/core/IconButton';
import Collapse from '@material-ui/core/Collapse';
import CloseIcon from '@material-ui/icons/Close';

class CustomAlert extends Component {

    state = {
        toggle: false
    }

    componentDidMount() {
        let { toggle } = this.props;
        this.setState({ toggle });
    }

    handleCollapse = (toggle) => this.setState({ toggle }, () => this.props.callbackOnClose && this.props.callbackOnClose());

    render() {
        let { message, title, type } = this.props;
        let { toggle } = this.state;
        return (
            <Collapse in={toggle} style={{ width: '100%' }}>
                <Alert
                    severity={type}
                    action={
                        <IconButton
                            aria-label="close"
                            color="inherit"
                            size="small"
                            onClick={() => this.handleCollapse(false)}
                        >
                            <CloseIcon fontSize="inherit" />
                        </IconButton>
                    }
                >
                    {title && <AlertTitle>{title}</AlertTitle>}
                    {message}
                </Alert>
            </Collapse>
        );
    }
}

CustomAlert.defaultProps = {
    message: '',        //  message needs to be shown in Alert.
    title: '',          //  message Title default will be blank.
    toggle: true,       //  default set to true to open Alert.
    type: 'info'        //  in ['error', 'warning', 'info', 'success']
}

CustomAlert.propTypes = {
    message: PropTypes.string.isRequired,
    title: PropTypes.string,
    toggle: PropTypes.bool,
    type: PropTypes.string,
    callbackOnClose: PropTypes.func
}

export default CustomAlert;