import React from 'react';
// import { Link } from 'react-router-dom';
import './style.scss';
import Button from '../../components/Button';
import UserTable from './component/Table';
// import UserTable from './table';
import { FormControl, InputLabel, Input, FormHelperText, TextField } from '@material-ui/core';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import {
  getProducts,
  getStateCity,
  placeNewOrder,
  refreshPlaceNewOrder,
  resetProductsSuccess,
  resetStateCity,
  getProductDetailsAction,
  verifyGSTDetails,
  resetGSTData
} from './action';
import { serachByOrderId, resetCustomerDetails } from '../Dashboard/action';
import { getUrlParameter } from '../../utilities/getUrlParamsValue';
import PageHeader from '../../components/PageHeader';
import AddItem from './component/AddItemPopup';
import {
  nameRegex,
  emailRegex,
  numberRegex,
  gstRegex,
  // addressRegex
} from '../../config/regex';
import { generateRandomNumber } from '../../utilities/generateRandomNumber';
import { getPaymentResourcesToShow } from '../../config/payments';

const supportedFloors = [
  "Ground Floor",
  "Floor-1",
  "Floor-2",
  "Floor-3",
  "Floor-4",
  "Floor-5",
  "Floor-6",
  "Floor-7",
  "Floor-8",
  "Floor-9",
  "Floor-10",
  "Floor-11",
  "Floor-12",
  "Floor-13",
  "Floor-14",
  "Floor-15",
  "Floor-16",
  "Floor-17",
  "Floor-18",
  "Floor-19",
  "Floor-20",
];
class PlaceNewOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      successOrderId: null,
      hasValidItem: false,
      enableSubmit: false,
      editingProduct: false,
      editingIndex: -1,
      showAddItem: false,
      selectedProduct: {},
      selectedProducts: [],
      netPrice: null,
      dimensions: [],
      dimensionSelected: '',
      errors: {},
      data: {
        firstName: "",
        lastName: "",
        email: "",
        phoneNumber: "",
        address: "",
        landmark: "",
        city: "",
        state: "",
        zipCode: "",
        alternatePhoneNo: "",
        floors: "",
        gstNo: "",
        gstOrg: "",
        additionalInfo: "",
        total: "",
        paymentType: "COD",
        payTrackId: "",
        paymentOption: ""
      },
      disableCOD: false,
      num1: 0,
      num2: 0,
      sum: ""
    };
  }

  toggleAddItem = (toggle) => this.setState({ showAddItem: toggle, hasValidItem: false, selectedProduct: !toggle ? {} : this.state.selectedProduct });

  getStateCity = (zipCode) => {
    let data = { "pincode": zipCode };
    this.props.getStateCity(data);
  }

  verifyGSTDetails = (gstNo) => {
    // console.log(gstNo)
    let data = { "gst_number": gstNo };
    this.props.verifyGSTDetails(data);
  }

  checkValidityOfForm = () => {
    let { data, errors, selectedProducts } = this.state;
    let hasErrors = false, validForm = false;
    let requiredFields = data.gstNo === "" ? ["firstName", "email", "phoneNumber", "address", "zipCode", "landmark", "floors"]
      : ["firstName", "email", "phoneNumber", "address", "zipCode", "landmark", "floors", "gstNo"]
    validForm = requiredFields.every(field => data[field] !== "");
    hasErrors = Object.keys(errors).length !== 0 && requiredFields.some(field => errors[field] && errors[field] !== "");
    this.setState({
      hasValidItem: false,
      enableSubmit:
        !hasErrors &&
        validForm &&
        selectedProducts.length > 0 &&
        Number(this.state.num1) + Number(this.state.num2) === Number(this.state.sum)
    });
  }

  checkValidityOfProduct = () => {
    this.setState({ hasValidItem: true });
  }

  addItem = (selectedProduct) => {
    let { selectedProducts, editingIndex } = this.state; // selectedProduct

    if (editingIndex !== -1) {
      selectedProducts[editingIndex] = selectedProduct;
    } else {
      selectedProducts.push(selectedProduct);
    }
    this.setState({ selectedProduct: {}, selectedProducts, editingIndex: -1 }, () => {
      this.calculateTotalCartPrice();
      this.toggleAddItem(false);
      this.checkValidityOfForm();
    });
  }

  removeProduct = (removalIndex) => {
    let { selectedProducts } = this.state;
    selectedProducts = selectedProducts.filter((product, index) => index !== removalIndex && product);
    this.setState({ selectedProducts }, () => {
      this.calculateTotalCartPrice();
      this.checkValidityOfForm();
    });
  };

  editProduct = (index) => {
    const selectedProduct = this.state.selectedProducts[index];
    this.setState({ editingProduct: true, editingIndex: index, selectedProduct }, () => {
      this.toggleAddItem(true);
      this.checkValidityOfForm();
      this.calculateTotalCartPrice();
    });
  }

  changePayment = (event) => {
    let { data } = this.state;
    data['paymentType'] = event.target.value;
    data['payTrackId'] = "";
    data['paymentOption'] = "";
    this.setState({ data });
  }

  handleSubmit = e => {
    e.preventDefault();
    let { data, selectedProducts } = this.state;
    this.setState({ successOrderId: null });

    let carts = selectedProducts.map((product) => {
      return {
        "item_sku": product.item_sku,
        "item_dimensions": product.item_dimensions ? product.item_dimensions + " inch" : product.item_dimensions,
        "item_model": product.item_model,
        "item_quantity": product.item_quantity,
        "item_price": product.item_price,
        "addon_extra_specifications": product.extra_specifications,
        "net_price": product.net_price
      }
    })
    let payload = {
      "addon": this.props.customerDetails && Object.keys(this.props.customerDetails).length > 0, // true for addon else false
      "order_id": this.props.orderId,
      "firstname": data.firstName,
      "lastname": data.lastName || "",
      "mobile_number": data.phoneNumber,
      "alternate_mobile_number": data.alternatePhoneNo,
      "email_address": data.email,
      "address": data.address,
      "city": data.city,
      "state": data.state,
      "floors": data.floors,
      "pincode": data.zipCode,
      "payment_type": data.paymentType,
      "payment_via": data.paymentOption,
      "landmark": data.landmark,
      "preferred_city": "",
      "payment_tracking_id": data.payTrackId,
      "additional_comments": data.additionalInfo,
      "gst_number": data.gstNo,
      "gst_organization": data.gstOrg,
      "carts": carts
    }
    this.props.placeNewOrder(payload);
  };

  getCustomerBasicDetails = (platform, orderId, role, type) => {
    this.props.serachByOrderId(platform, orderId, role, type);
  }

  componentDidMount() {
    if (this.props.orderId) {
      let role = "";
      let type = getUrlParameter("type");
      type && this.getCustomerBasicDetails(this.props.platform, this.props.orderId, role, type);
    }
    this.setState({ num1: generateRandomNumber(1, 9), num2: generateRandomNumber(1, 9) });
    this.props.getProducts();
  }

  componentWillUnmount() {
    this.resetPlaceNewOrder();
    this.props.resetCustomerDetails();
    this.props.resetProductsSuccess();
    this.props.resetStateCity();
    this.props.resetGSTData();
  }

  closeSuccessMessage = () => {
    this.setState({ successOrderId: null });
  }

  componentWillReceiveProps(props) {
    // console.log(props)
    if (props.response && !this.state.successOrderId) {
      this.setState({ successOrderId: props.response.order_id });
      window.scrollTo(0, 0);
    }
    if (props.customerDetails && Object.keys(props.customerDetails).length > 0) {
      let { data } = this.state;
      let { customerDetails } = props;

      //  Remove Floor
      let address = customerDetails.address;
      if (customerDetails.floors && address.includes(customerDetails.floors))
        address = address.split(customerDetails.floors).join("");

      [...supportedFloors].reverse().map((floor, index) => {
        if (address.includes(floor)) {
          address = address.split(floor).join("").split(",").join("").trim();
        }
        return "";
      })

      data = {
        ...data,
        firstName: customerDetails.firstname,
        lastName: customerDetails.lastname || "",
        email: customerDetails.email_address,
        phoneNumber: customerDetails.mobile_number,
        alternatePhoneNo: customerDetails.alternate_mobile_number,
        landmark: customerDetails.landmark,
        floors: customerDetails.floors,
        address: address,
        zipCode: customerDetails.pincode,
        city: customerDetails.city,
        state: customerDetails.state,
        // preferredCity: customerDetails.preferred_city
      }
      this.setState({ data });
    }

    if (props.zipInfo) {
      let { data, errors } = this.state;
      // console.log(props.zipInfo)
      if (props.zipInfo.message) {
        errors.zipCode = props.zipInfo.message;
        data["city"] = "";
        data["state"] = "";
        this.setState({ data, errors }, () => this.checkValidityOfForm());
      } else if (props.zipInfo) {
        data["city"] = props.zipInfo.city || "";
        data["state"] = props.zipInfo.state || "";
        data["paymentType"] = props.zipInfo && props.zipInfo.cod_avaibility && props.zipInfo.cod_avaibility === "NO" ? "ONLINE" : data["paymentType"];
        errors.zipCode = "";
        this.setState({ data, errors }, () => this.checkValidityOfForm());
        // this.setState({ data: { ...data, ...props.zipInfo, "paymentType": props.zipInfo && props.zipInfo.cod_avaibility === "NO" ? "ONLINE" : data["paymentType"] } }, () => this.checkValidityOfForm());
      }
    }

    if (props.gstInfo) {
      let { data, errors } = this.state;
      if (props.gstInfo.message) {
        errors.gstNo = props.gstInfo.message;
        data["gstOrg"] = "";
        this.setState({ data, errors },
          () => this.checkValidityOfForm()
        );
      } else if (props.gstInfo) {
        // errors.gstNo = "";
        errors.gstNo = props.gstInfo.gstn_status && String(props.gstInfo.gstn_status).toLocaleLowerCase() === "active" ? "" : "Inactive/ Invalid GST Number";
        data["gstOrg"] = props.gstInfo.legal_name_of_business;
        this.setState({ data, errors },
          () => this.checkValidityOfForm()
        )
      }
    }

    if (props.resetData) {
      this.resetPlaceNewOrder();
    }
  }

  resetPlaceNewOrder = () => {
    let data = {
      firstName: "",
      lastName: "",
      email: "",
      phoneNumber: "",
      address: "",
      landmark: "",
      city: "",
      state: "",
      zipCode: "",
      alternatePhoneNo: "",
      floors: "",
      gstNo: "",
      gstOrg: "",
      additionalInfo: "",
      total: "",
      paymentType: "",
      payTrackId: "",
      paymentOption: ""
    }
    //  reset
    this.setState({
      data: { ...data },
      selectedProducts: [],
      selectedProduct: {},
      num1: generateRandomNumber(1, 9), num2: generateRandomNumber(1, 9), sum: 0
    }, () => {
      this.props.refreshPlaceNewOrder(false);
      this.props.resetCustomerDetails();
    });
  }

  handleChange = e => {
    e.preventDefault();
    let fieldName = e.target.name;
    let fieldValue = e.target.value;
    let { data, errors } = this.state;

    switch (fieldName) {
      case 'firstName':
        if (!fieldValue) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "First Name is Required!";
        } else if (nameRegex.test(fieldValue) && fieldValue.length >= 3) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (nameRegex.test(fieldValue) && fieldValue.length < 3) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Minimum of 3 Characters are required!";
        }
        break;

      case 'lastName':
        // if (!fieldValue) {
        //   data[fieldName] = fieldValue;
        //   errors[fieldName] = "Last Name is Required!";
        // } else 
        if (nameRegex.test(fieldValue)) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
          // } else if (nameRegex.test(fieldValue) && fieldValue.length < 3) {
          //   data[fieldName] = fieldValue;
          //   errors[fieldName] = "Minimum of 3 Characters are required!";
        }
        break;

      case 'email':
        if (!fieldValue) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Email is Required!";
        } else if (emailRegex.test(fieldValue)) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (!emailRegex.test(fieldValue)) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Invalid Email Id!";
        }
        break;

      case 'phoneNumber':
        if (numberRegex.test(fieldValue) && fieldValue.length === 10) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (numberRegex.test(fieldValue) && fieldValue.length >= 1 && fieldValue.length < 10) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Invalid Mobile Number!";
        } else if (fieldValue.length === 0) {
          data[fieldName] = "";
          errors[fieldName] = "Mobile Number Required!";
        }
        break;

      case 'address':
        if (fieldValue && fieldValue.length >= 5) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (fieldValue && fieldValue.length < 5) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Invalid Address!";
        } else if (fieldValue.length === 0) {
          data[fieldName] = "";
          errors[fieldName] = "Address is Required!";
        }
        break;

      case "landmark":
        if (!fieldValue) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Landmark is Required!";
        } else if (fieldValue && fieldValue.length >= 3) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (fieldValue && fieldValue.length < 3) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Minimum of 3 Characters are required!";
        }
        break;

      case "floors":
        if (fieldValue) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (!fieldValue) {
          data[fieldName] = "";
          errors[fieldName] = "Floors is Required!";
        }
        break;

      case 'zipCode':
        if (numberRegex.test(fieldValue) && fieldValue.length === 6) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
          // Get State and City
          this.getStateCity(fieldValue);
        } else if (numberRegex.test(fieldValue) && fieldValue.length < 6) {
          data[fieldName] = fieldValue;
          data["city"] = "";
          data["state"] = "";
          errors[fieldName] = "Invalid ZIP Code!";
        } else if (fieldValue.length === 0) {
          data[fieldName] = "";
          data["city"] = "";
          data["state"] = "";
          errors[fieldName] = "ZIP Code is Required!";
        }
        break;

      case 'gstNo':
        let gst = fieldValue.toLocaleUpperCase();
        if (gst.length === 0) {
          data[fieldName] = "";
          data["gstOrg"] = "";
          errors[fieldName] = "";
          this.props.resetGSTData();
        }
        else if (gstRegex.test(gst) && gst.length === 15) {
          data[fieldName] = gst;
          errors[fieldName] = "";
          this.verifyGSTDetails(gst);
        }
        else if (!gstRegex.test(gst)) {
          data[fieldName] = gst;
          data["gstOrg"] = "";
          this.props.resetGSTData();
          errors[fieldName] = "Invalid GST number!";
        }
        break;

      case 'alternatePhoneNo':
        if (numberRegex.test(fieldValue) && fieldValue.length === 10) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "";
        } else if (numberRegex.test(fieldValue) && fieldValue.length >= 1 && fieldValue.length < 10) {
          data[fieldName] = fieldValue;
          errors[fieldName] = "Invalid Alternate Mobile Number!";
        } else if (fieldValue.length === 0) {
          data[fieldName] = "";
          errors[fieldName] = "";
          // errors[fieldName] = "Alternate Mobile Number Required!";
        }
        break;

      case 'payTrackId': data[fieldName] = fieldValue;
        break;

      case 'paymentOption': data[fieldName] = fieldValue;
        break;

      default: data[fieldName] = fieldValue;
        break;
    }
    this.setState({ data, errors }, () => {
      // console.log(this.state.data, this.state.errors)
      this.checkValidityOfForm();
    });
  }

  calculateTotalCartPrice = () => {
    let { selectedProducts, data, disableCOD } = this.state;
    let total = 0;

    //  For Cart Price >= 20000 Disable COD.
    // for (let i = 0; i < selectedProducts.length; i++) {
    //   if (selectedProducts[i]["net_price"] >= 20000) {
    //     disableCOD = true;
    //     break;
    //   } else {
    //     disableCOD = false;
    //   }
    // }

    selectedProducts.map(product => total += Number(product["net_price"]));

    //  For Total Price of Order >= 20000 Disable COD.
    if (total >= 20000) {
      disableCOD = true;
    } else {
      disableCOD = false;
    }

    // Set Payment Type based on 'disableCOD' and 'cod_avaibility' at given PIN Code.
    if (disableCOD || (this.props.zipInfo && this.props.zipInfo.cod_avaibility === "NO"))
      data["paymentType"] = "ONLINE";
    else {
      data["paymentType"] = "COD";
      data["paymentOption"] = "";
      data["payTrackId"] = "";
    }

    this.setState({ data, total, disableCOD });
  }

  getProductsListTabel = () => <div>
    <div className="product-list-table-header">
      <div className="header">Product Details</div>
      <Button
        label="Add"
        onClick={() => this.toggleAddItem(true)}
      />
    </div>
    <UserTable
      netPrice={this.state.total}
      disableCOD={this.state.disableCOD}
      products={this.state.selectedProducts}
      removeProduct={this.removeProduct}
      editProduct={this.editProduct}
      placeOrderError={this.props.placeOrderError}
    />
  </div>

  storeSum = (event) => {
    // let numberRegex = /^[0-9]*$/;
    let { sum } = this.state;
    this.setState({ sum: numberRegex.test(event.target.value) ? event.target.value : sum }, () => this.checkValidityOfForm());
  }

  render() {
    const { data, errors } = this.state;
    let title = (this.props.customerDetails && Object.keys(this.props.customerDetails).length > 0) ? "Add an Order" : "Place New Order";
    return (
      <div className="place-new-order-container">
        <PageHeader
          header={title}
        />
        {this.state.successOrderId &&
          <div className="order-response">
            <div className="response-content">Successfully placed new order with Order Id: <b style={{ fontWeight: "700" }}>  {this.state.successOrderId}</b>
              {/* <b><Link to={`/universal-order-search/${this.state.successOrderId}`} >{this.state.successOrderId}</Link></b> */}
            </div>
            <div className="close-btn" onClick={this.closeSuccessMessage}><i className="fa fa-times" aria-hidden="true"></i></div>
          </div>}
        <div className="place-new-order-content">
          {this.getProductsListTabel()}
        </div>
        <div className="form-container">
          <div className="form-content">
            <div className="header">Address Information</div>
            <div className="addcontent">
              <div className="left-side">
                <FormControl className="full-width form-control-custom" error={errors.firstName}>
                  <InputLabel htmlFor="firstName">First Name *</InputLabel>
                  <Input name="firstName" type="text" variant="outlined" required="true" value={data.firstName} onChange={this.handleChange}
                    aria-describedby="firstName-text" autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="firstName-text">{errors.firstName}</FormHelperText>
                </FormControl>
              </div>

              <div className="right-side">
                <FormControl className="full-width form-control-custom" error={errors.lastName}>
                  <InputLabel htmlFor="lasstName">Last Name</InputLabel>
                  <Input name="lastName" type="text" variant="outlined" required={true} value={data.lastName} onChange={this.handleChange}
                    aria-describedby="lastName-text" autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="lastName-text">{errors.lastName}</FormHelperText>
                </FormControl>
              </div>

              <div className="left-side">
                <FormControl className="full-width form-control-custom" error={errors.email}>
                  <InputLabel htmlFor="email">Email *</InputLabel>
                  <Input
                    id="left"
                    name="email"
                    type="email"
                    variant="filled"
                    required={true}
                    value={data.email}
                    onChange={this.handleChange}
                    aria-describedby="email-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="email-text">{errors.email}</FormHelperText>
                </FormControl>
              </div>

              <div className="right-side">
                <FormControl className="full-width form-control-custom" error={errors.phoneNumber}>
                  <InputLabel htmlFor="">Phone Number *</InputLabel>
                  <Input
                    id="right"
                    name="phoneNumber"
                    type="text"
                    variant="outlined"
                    value={data.phoneNumber}
                    onChange={this.handleChange}
                    aria-describedby="pno-text"
                    maxLength={10}
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="pno-text">{errors.phoneNumber}</FormHelperText>
                </FormControl>
              </div>
              <FormControl className="full-width form-control-custom" error={errors.address}>
                <TextField
                  id="standard-multiline-static"
                  label="Address *"
                  name="address"
                  multiline
                  rows="4"
                  onChange={this.handleChange}
                  defaultValue="Default Value"
                  value={data.address}
                  className=""
                />
                <FormHelperText className="error-text" id="address-text">{errors.address}</FormHelperText>
              </FormControl>
              <div className="left-side">
                <FormControl className="full-width form-control-custom" error={errors.landmark}>
                  <InputLabel htmlFor="">LandMark *</InputLabel>
                  <Input
                    id="left"
                    name="landmark"
                    type="text"
                    variant="outlined"
                    value={data.landmark}
                    onChange={this.handleChange}
                    aria-describedby="land-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="land-text">{errors.landmark}</FormHelperText>
                </FormControl>
              </div>
              {/* <div className="right-side">
                <FormControl className="full-width form-control-custom">
                  <InputLabel htmlFor="">Select floor</InputLabel>
                  <Input
                    id="right"
                    name="floors"
                    type="text"
                    variant="outlined"
                    value={data.floors}
                    onChange={this.handleChange}
                    aria-describedby="floor-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="floor-text"></FormHelperText>
                </FormControl>
              </div> */}

              <div className="right-side" style={{ height: "53px", paddingTop: "6px" }} error={errors.floors}>
                <select
                  name="floors"
                  id="right"
                  className="custom-dropdown"
                  onChange={this.handleChange}
                  value={data.floors}
                  style={{
                    border: "none", paddingLeft: "0px",
                    borderBottom: "1px solid rgba(0, 0, 0, 0.42)",
                    color: "rgba(0, 0, 0, 0.54)",
                    marginBottom: "0px"
                  }}
                >
                  <option value="">Select Floor *</option>
                  {supportedFloors.map((floor, index) => <option key={floor} value={floor}>{floor}</option>)}
                </select>
                <FormHelperText className="error-text" id="land-text">{errors.floors}</FormHelperText>
              </div>

              <div className="left-side">
                <FormControl className="full-width form-control-custom" error={errors.zipCode}>
                  <InputLabel htmlFor="">ZIP Code *</InputLabel>
                  <Input
                    id="right"
                    name="zipCode"
                    type="text"
                    variant="outlined"
                    value={data.zipCode}
                    onChange={this.handleChange}
                    aria-describedby="zip-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="address-text">{errors.zipCode}</FormHelperText>
                </FormControl>
              </div>
              <div className="right-side">
                <FormControl className="full-width form-control-custom" error={errors.alternatePhoneNo}>
                  <InputLabel htmlFor="">Alt Phone Number</InputLabel>
                  <Input
                    id="right"
                    name="alternatePhoneNo"
                    type="text"
                    variant="outlined"
                    value={data.alternatePhoneNo}
                    onChange={this.handleChange}
                    aria-describedby="alternatePhoneNo-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="alternatePhoneNo-text">{errors.alternatePhoneNo}</FormHelperText>
                </FormControl>
              </div>
              <div className="left-side">
                <FormControl className="full-width form-control-custom">
                  <InputLabel htmlFor="">City</InputLabel>
                  <Input
                    id="right"
                    name="city"
                    type="text"
                    disabled
                    variant="outlined"
                    value={data.city}
                    onChange={this.handleChange}
                    aria-describedby="city-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="floor-text"></FormHelperText>
                </FormControl>
              </div>
              <div className="right-side">
                <FormControl className="full-width form-control-custom">
                  <InputLabel htmlFor="">State</InputLabel>
                  <Input
                    id="left"
                    name="state"
                    type="text"
                    disabled
                    variant="outlined"
                    value={data.state}
                    onChange={this.handleChange}
                    aria-describedby="state-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="floor-text"></FormHelperText>
                </FormControl>
              </div>
              <br />

              <div className="left-side">
                <FormControl className="full-width form-control-custom" error={errors.gstNo}>
                  <InputLabel htmlFor="">GST Number</InputLabel>
                  <Input
                    id="left"
                    name="gstNo"
                    type="text"
                    variant="outlined"
                    value={data.gstNo}
                    onChange={this.handleChange}
                    aria-describedby="gstNo-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="gstNo-text">{errors.gstNo}</FormHelperText>
                </FormControl>
              </div>
              <div className="right-side">
                <FormControl className="full-width form-control-custom">
                  <InputLabel htmlFor="">GST Organisation</InputLabel>
                  <Input
                    id="left"
                    name="gstOrg"
                    variant="outlined"
                    value={data.gstOrg}
                    disabled
                    onChange={this.handleChange}
                    aria-describedby="gstOrg-text"
                    autoFocus={false}
                  />
                  <FormHelperText className="error-text" id="floor-text"></FormHelperText>
                </FormControl>
              </div>
            </div>
            <br />
          </div>

          <div>
            <div className="form-content">
              <div className="header">Payment Details</div>
              <div className="addcontent">
                {this.props.zipInfo && this.props.zipInfo.cod_avaibility === "NO" &&
                  <div style={{ color: "red", fontSize: "11px", paddingBottom: "4px" }}>COD is not available at given Pincode</div>
                }
                <div style={{ paddingBottom: "7px", fontSize: "13px" }}>Payment Type *</div>
                <div style={{ display: 'flex' }}>
                  <div style={{ display: 'flex' }}>
                    <input name="paymentType" id="payment_type_online" type="radio" onChange={this.changePayment} checked={data.paymentType === "ONLINE"} value="ONLINE" />
                    <label for="payment_type_online">Online</label>
                  </div>
                  {!(this.state.disableCOD || (this.props.zipInfo && this.props.zipInfo.cod_avaibility === "NO")) ? <div className="paymentMode">
                    <input name="paymentType" id="payment_type_cod" type="radio" onChange={this.changePayment} checked={data.paymentType === "COD"} value="COD" />
                    <label for="payment_type_cod">COD</label>
                  </div> : ""}
                </div>
                {data["paymentType"] === "ONLINE" ?
                  <div id="results" className="search-results">
                    <select className="custom-dropdown" name="paymentOption" onChange={this.handleChange}>
                      <option value="">Select</option>
                      {getPaymentResourcesToShow('placeOrder').map(resource => <option>{resource.name}</option>)}
                    </select>
                    <TextField label="Payment Track Id" className="paytrack" name="payTrackId" onChange={this.handleChange} />
                  </div>
                  : null}
              </div>

              <div className="addcontent">
                <TextField
                  id="standard-multiline-static"
                  label="Additional Information"
                  name="additionalInfo"
                  multiline
                  rows="3"
                  onChange={this.handleChange}
                  value={data.additionalInfo}
                  className=""
                  fullWidth
                />

                <div className='summation-box'>
                  <div className="numbers">{this.state.num1}</div>
                  <div className="symbol">+</div>
                  <div className="numbers">{this.state.num2}</div>
                  <div className="symbol">=</div>
                  <input className="sum-input" maxLength="2" onChange={this.storeSum} value={this.state.sum} placeholder="Result" />
                </div>

                <div className="createAccount">
                  <Button
                    type="submit"
                    label="Place Order"
                    disabled={!this.state.enableSubmit}
                    onClick={this.handleSubmit}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <AddProductPopup */}
        <AddItem
          toggleAddItem={this.toggleAddItem}
          showAddItem={this.state.showAddItem}
          selectedProduct={this.state.selectedProduct}
          addItem={this.addItem}
          editingIndex={this.state.editingIndex}
        />
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    productDetails: state.placeNewOrder.productDetails,
    zipInfo: state.placeNewOrder.zipInfo,
    gstInfo: state.placeNewOrder.gstInfo,
    resetData: state.placeNewOrder.resetData,
    response: state.placeNewOrder.response,
    placeOrderError: state.placeNewOrder.placeOrderError,
    customerDetails: state.general.customerDetails,
    userDetails: state.userDetails
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    getProducts,
    getStateCity,
    placeNewOrder,
    refreshPlaceNewOrder,
    serachByOrderId,
    resetCustomerDetails,
    resetProductsSuccess,
    resetStateCity,
    getProductDetailsAction,
    verifyGSTDetails,
    resetGSTData
  }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(PlaceNewOrder);
