import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import './style.scss';
import { Dialog, IconButton, FormControlLabel, Checkbox, CircularProgress } from '@material-ui/core';
import { Add, Close } from '@material-ui/icons';
import Button from '../../../../components/Button';
import { getDimensionsForProductSku } from '../../../../config/dimensions';
import { decimalRegex, dimensionRegex, numberRegex } from '../../../../config/regex';
import { availableModels, availableColors, availableTypes } from '../../../../config/productModelsTypesAndColor';
import { getProductDetailsAction, resetPlaceNewOrderSuccessAndError } from '../../action';
import { productDetailsAPI } from '../../../../api';
import CustomAlert from '../../../../components/Alert';
import { removeDot } from '../../../../utilities/common';

const productsHas3Dimensions = ["WDCM", "WOMFM", "WHRFM", "WFT", "WFOCOVER"];
const productsHas2Dimensions = ["WFB", "WMP", "WSWB"];
const disableCustomization = ["WSWB", "WFB", "WMP"];

const productDetails = [
    { label: "Wakefit Dual Comfort Mattress", sku: "WDCM" },
    { label: "Wakefit Orthopaedic Memory Foam Mattress", sku: "WOMFM" },
    { label: "Wakefit Water Proof Mattress Protector", sku: "WMP" },
    { label: "Wakefit Sleeping Pillow", sku: "WSP" },
    { label: "Wakefit Sheesham Wood Bed", sku: "WSWB" },
    { label: "Wakefit Back Support Cushion", sku: "WBSC" },
    { label: "Wakefit Travel Neck Pillow", sku: "WTNP" },
    { label: "Wakefit Fitted Bedsheet", sku: "WFB" },
    { label: "Wakefit Reversible Comforters", sku: "WSMRC" },
    { label: "Wakefit Full HR Foam Mattress", sku: "WHRFM" },
    { label: "Wakefit Outer Cover", sku: "WFOCOVER" },
    { label: "Wakefit Mattress Topper", sku: "WFT" }
];

class AddItem extends React.Component {

    state = {
        selectedProduct: {
            item_sku: "",
            item_dimensions: "",
            item_quantity: 1,
            item_price: "",
        },
        errors: {
            item_sku: "",
            item_dimensions: "",
            item_quantity: "",
            item_price: "",
        },
        maxQty: 0,
        stockData: "notvalid",  //  It should be in ["notvalid", "valid"]
        enableSubmit: false
    }

    componentDidMount() {
        let { selectedProduct } = this.state;
        this.setState({
            selectedProduct: {
                ...selectedProduct,
                ...this.props.selectedProduct
            }
        })
    }

    componentWillReceiveProps(props) {
        this.setState({
            selectedProduct: {
                ...props.selectedProduct,
                item_price: ""
            },
            errors: {},
            stockData: "notvalid"
        })
    }

    componentWillUnmount() {
        this.setState({
            selectedProduct: {
                item_sku: "",
                item_dimensions: "",
                item_quantity: "",
                item_price: "",
            },
            errors: {
                item_sku: "",
                item_dimensions: "",
                item_quantity: "",
                item_price: "",
            }
        });
    }

    getQuantityOptions = () => {
        let options = [];
        let { maxQty } = this.state;
        for (let i = 1; i <= maxQty; i++) options.push(<option>{i}</option>)
        return options;
    }

    getProductDetails = () => {
        let { selectedProduct, errors, maxQty, stockData } = this.state;
        // let { userDetails } = this.props;
        console.log("Getting...", this.state);
        let payload = {
            "item_sku": removeDot(selectedProduct.item_sku),
            "item_dimensions": selectedProduct.item_dimensions ? selectedProduct.item_dimensions + " inch" : "",
            "affiliate_id": 0,
            "request_type": 1
        };

        this.setState({ stockData: "loading" }, () => {
            productDetailsAPI(payload)
                .then((response) => {
                    console.log(response);

                    //  Store price
                    selectedProduct.item_price = response.data && response.data.item_price && response.data.item_price.price;
                    errors.item_price = "";
                    errors.note = "";

                    // Validate the stock
                    // if (userDetails && userDetails.role && userDetails.role.includes('PARTNERSHIPS')) {
                    //     stockData = "valid";
                    // } else {
                    if (Number(response.data.stock) === 0) {
                        maxQty = 0;
                        errors.note = "Out of Stock";
                        stockData = "notvalid";
                    } else if (Number(response.data.stock) < 20) {
                        maxQty = Number(response.data.stock);
                        stockData = "valid";
                    } else if (Number(response.data.stock) >= 20) {
                        maxQty = 20;
                        stockData = "valid";
                    }
                    // }

                    setTimeout(() => this.setState({ selectedProduct, errors, maxQty: maxQty, stockData: stockData, stock: response.data }), 1000);
                })
                .catch((error) => {
                    console.log(error.response);
                    selectedProduct.item_price = "";
                    errors.note = error.response && error.response.data && error.response.data.message;
                    setTimeout(() => this.setState({ selectedProduct, errors, maxQty: 20, stockData: "notvalid" }), 1000);
                });
        });
        // this.props.getProductDetailsAction &&
        //     this.props.getProductDetailsAction(payload);
    }

    checkValidity = (event) => {
        event.preventDefault();
        let { selectedProduct, errors } = this.state;

        let requiredFields = ["product", "item_quantity", "item_price"];

        let hasRequiredFields = requiredFields.every(field => selectedProduct[field]);

        let hasDimensionOrType = false;

        errors.dimension = "";

        if (!selectedProduct["product"]) {
            errors["product"] = "Please select the Product!";
        }

        if (!selectedProduct["item_quantity"]) {
            errors["item_quantity"] = "Item Qty is Required!";
            hasRequiredFields = false;
        } else if (this.state.stock && Number(this.state.stock.stock) < Number(selectedProduct["item_quantity"])) {
            errors["item_quantity"] = "Should be <= stock";
            hasRequiredFields = false;
        }

        if (!selectedProduct["item_price"]) {
            errors["item_price"] = "Item Price is Required!";
            hasRequiredFields = false;
        }

        if (["WDCM", "WOMFM", "WMP", "WSWB"].includes(selectedProduct["product"])) {
            //  products having dimension imp
            if (selectedProduct.dimension) {
                hasDimensionOrType = true;
                errors.dimension = "";
            } else {
                errors.dimension = "Please select the Dimension!";
            }
        } else if (["WSP"].includes(selectedProduct["product"])) {
            //  products having type imp
            if (selectedProduct.type) {
                hasDimensionOrType = true;
                errors.type = "";
            } else {
                errors.type = "Please select the Type!";
            }
        } else if (["WFB"].includes(selectedProduct["product"])) {
            //  products having both dimension and modal
            if (selectedProduct.dimension && selectedProduct.modal) {
                hasDimensionOrType = true;
                errors.dimension = "";
            } else if (!selectedProduct.dimension) {
                errors.dimension = "Please select the Dimension!";
            } else if (!selectedProduct.modal) {
                errors.modal = "Please select the Modal!";
            }
        } else if (["WSMRC"].includes(selectedProduct["product"])) {
            //  products having type and modal
            if (selectedProduct.type && selectedProduct.modal) {
                hasDimensionOrType = true;
                errors.dimension = "";
            } else if (!selectedProduct.modal) {
                errors.modal = "Please select the Modal!";
            } else if (!selectedProduct.type) {
                errors.type = "Please select the Type!";
            }
        } else if (["WHRFM"].includes(selectedProduct["product"])) {
            //  products having modal or customized dimension
            if (selectedProduct.length && selectedProduct.breath && selectedProduct.height) {
                hasDimensionOrType = true;
            } else {
                if (!selectedProduct.length) {
                    errors.length = "Required!";
                }
                if (!selectedProduct.breath) {
                    errors.breath = "Required!";
                }
                if (!selectedProduct.height) {
                    errors.height = "Required!";
                }
            }
        } else if (["WFT"].includes(selectedProduct["product"])) {
            //  products having modal or customized dimension
            if ((selectedProduct.length && selectedProduct.breath && selectedProduct.height) &&
                selectedProduct.modal) {
                hasDimensionOrType = true;
            } else if (!selectedProduct.modal) {
                errors.modal = "Please select the Modal!";
            }
            // else {
            if (!selectedProduct.length) {
                errors.length = "Required!";
            }
            if (!selectedProduct.breath) {
                errors.breath = "Required!";
            }
            if (!selectedProduct.height) {
                errors.height = "Required!";
            }
            // }
        } else if (["WBSC", "WTNP"].includes(selectedProduct["product"])) {
            //  products only required don't have dimension or modal or type
            hasDimensionOrType = true;
        } else if (["WFOCOVER"].includes(selectedProduct["product"])) {
            //  products having customized dimension
            if ((selectedProduct.length && selectedProduct.breath && selectedProduct.height) &&
                selectedProduct.modal) {
                hasDimensionOrType = true;
            }
            if (!selectedProduct.modal) {
                errors.modal = "Please select the Modal!";
            }
            // else {
            if (!selectedProduct.length) {
                errors.length = "Required!";
            }
            if (!selectedProduct.breath) {
                errors.breath = "Required!";
            }
            if (!selectedProduct.height) {
                errors.height = "Required!";
            }
            // }
        }

        // if (hasDimensionOrType) {
        //     this.getProductDetails();
        // }

        this.setState({ errors, enableSubmit: hasRequiredFields && hasDimensionOrType, note: "" }, () => {
            if (hasDimensionOrType) {
                console.log("Valid...");
                if (hasRequiredFields && this.state.stockData === 'valid') {
                    this.props.resetPlaceNewOrderSuccessAndError();
                    this.saveProduct();
                } else if (this.state.stockData === 'notvalid') {
                    this.getProductDetails();
                }
            }
        });
    }

    getMinimumPrice = () => {
        let price = 0;
        let { productDetails } = this.props;
        let { selectedProduct } = this.state;
        let sku = selectedProduct["product"];

        //  gets Minimum Price
        if (productDetails &&
            productDetails["product_details"][sku] &&
            productDetails["product_details"][sku].dimensionAndPrice &&
            productDetails["product_details"][sku].dimensionAndPrice.length > 0) {

            price = productDetails["product_details"][sku].dimensionAndPrice[0].price;

            //  Get price according to dimension/ type or modal etc will goes here.
            productDetails["product_details"][sku].dimensionAndPrice.map((itemDimension) => {
                if (selectedProduct["dimension"] && itemDimension.dimension.includes(selectedProduct["dimension"])) {
                    //  If Dimension
                    price = itemDimension.price;
                } else if (selectedProduct["type"] && itemDimension.value === selectedProduct["type"]) {
                    //  type based price like pillow/ Comforters
                    price = itemDimension.price;
                } else if (selectedProduct["modal"] && itemDimension.value === selectedProduct["modal"]) {
                    //  Modal based price
                    price = itemDimension.price;
                }
                return "";
            });
        }

        return price;
    }

    saveProduct = () => {
        let { selectedProduct } = this.state;
        let payload = selectedProduct;

        payload.net_price = Number(payload.item_quantity) * Number(payload.item_price);

        this.props.addItem(payload);
        this.setState({ selectedProduct: {}, enableSubmit: false });
    }

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;
        let { selectedProduct, errors, stockData, stock } = this.state;

        switch (fieldName) {
            case "product":
                if (fieldValue) {
                    selectedProduct[fieldName] = fieldValue;
                    errors = {};
                } else {
                    selectedProduct[fieldName] = fieldValue;
                    errors[fieldName] = "Please select the Product";
                }
                selectedProduct["item_sku"] = selectedProduct["product"];
                selectedProduct["item_dimensions"] = "";
                // selectedProduct["item_price"] = this.getMinimumPrice();
                selectedProduct["item_price"] = 0;
                stock = null;
                stockData = "notvalid";
                selectedProduct["item_quantity"] = selectedProduct["item_quantity"] || 0;
                selectedProduct["dimension"] = "";
                selectedProduct["modal"] = "";
                selectedProduct["type"] = "";

                selectedProduct["extra_specifications"] = "";

                selectedProduct["length"] = "";
                selectedProduct["breath"] = "";
                selectedProduct["height"] = "";

                selectedProduct["supportedDimensions"] = getDimensionsForProductSku(fieldValue);
                break;

            case "dimension":
                if (fieldValue) {
                    selectedProduct[fieldName] = fieldValue;
                    errors[fieldName] = "";

                    let dimensionArray = fieldValue.split("x");
                    let regexToCheckCharAtEnd = /[a-zA-Z]*$/;
                    dimensionArray.length > 0 && dimensionArray.map(dimension => {
                        if (regexToCheckCharAtEnd.test(dimension)) {
                            selectedProduct["modal"] = dimension.replace(/[^a-zA-Z]+/g, '')
                        }
                        return "";
                    })
                    selectedProduct["length"] = (dimensionArray.length > 1 && (dimensionArray[0].replace(/[^\d]/g, ''))) || "";
                    selectedProduct["breath"] = (dimensionArray.length >= 2 && (dimensionArray[1].replace(/[^\d]/g, ''))) || "";
                    selectedProduct["height"] = (dimensionArray.length >= 3 && (dimensionArray[2].replace(/[^\d]/g, ''))) || "";

                    errors["length"] = errors["breath"] = errors["height"] = "";

                    // selectedProduct["item_price"] = this.getMinimumPrice();
                    selectedProduct["item_price"] = 0;
                    stock = null;
                    stockData = "notvalid";
                } else {
                    selectedProduct[fieldName] = fieldValue;
                    errors[fieldName] = "Please select the Dimension";
                }
                break;

            case "modal":
                selectedProduct[fieldName] = fieldValue;
                errors[fieldName] = !selectedProduct["product"].includes("WMP") && (fieldValue ? "" : "Required!");
                // selectedProduct["item_price"] = this.getMinimumPrice();
                selectedProduct["item_price"] = 0;
                stock = null;
                stockData = "notvalid";
                break;

            case "type":
                selectedProduct[fieldName] = fieldValue;
                errors[fieldName] = fieldValue ? "" : "Required!";
                // selectedProduct["item_price"] = this.getMinimumPrice();
                selectedProduct["item_price"] = 0;
                stock = null;
                stockData = "notvalid";
                break;

            case "item_price":
                if (fieldValue && decimalRegex.test(fieldValue)) {
                    selectedProduct[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (!fieldValue) {
                    selectedProduct[fieldName] = "";
                    errors[fieldName] = "Required!";
                }

                selectedProduct[fieldName] = fieldValue !== "" ?
                    decimalRegex.test(fieldValue) ? fieldValue : selectedProduct[fieldName]
                    : "";
                break;

            case "item_quantity":
                if (fieldValue && numberRegex.test(fieldValue) && fieldValue.length < 4) {
                    selectedProduct[fieldName] = fieldValue;
                    errors[fieldName] = this.state.stock && Number(this.state.stock.stock) >= Number(fieldValue) ? "" : "Should be <= stock";
                } else if (!fieldValue) {
                    selectedProduct[fieldName] = "";
                    errors[fieldName] = "Item Qty is Required!";
                }
                break;

            case "length":
            case "breath":
            case "height":
                if (fieldValue) {

                    if (selectedProduct["supportedDimensions"] &&
                        selectedProduct["supportedDimensions"].length > 0 &&
                        !selectedProduct.dimension) {
                        errors.dimension = "Please select the dimension";
                    }

                    selectedProduct[fieldName] = fieldValue !== "" ?
                        dimensionRegex.test(fieldValue) ? fieldValue : selectedProduct[fieldName]
                        : "";
                    errors[fieldName] = "";
                } else {
                    selectedProduct[fieldName] = fieldValue;
                    errors[fieldName] = "Required!";
                }
                stockData = "notvalid";
                selectedProduct.item_price = "";
                break;

            default: selectedProduct[fieldName] = fieldValue;
                break;
        }

        if (selectedProduct["product"] === "WSMRC") {
            selectedProduct["item_dimensions"] = (selectedProduct["type"] === "S" ? "60x90" : selectedProduct["type"] === "D" ? "100x90" : "");
        } else {
            selectedProduct["item_dimensions"] = `${selectedProduct["length"]}${selectedProduct["breath"] ? ("x" + selectedProduct["breath"]) : ""}${selectedProduct["height"] ? "x" + selectedProduct["height"] : ""}`;
        }

        if (["WFT", "WMP", "WFOCOVER"].includes(selectedProduct["product"])) {
            selectedProduct["item_sku"] = selectedProduct["product"] + selectedProduct["modal"] + (selectedProduct["length"] && selectedProduct["length"].split(".").join("")) + (selectedProduct["breath"] && selectedProduct["breath"].split(".").join("")) + (selectedProduct["height"] && selectedProduct["height"].split(".").join(""));
        } else {
            selectedProduct["item_sku"] = selectedProduct["product"] + (selectedProduct["length"] && selectedProduct["length"].split(".").join("")) + (selectedProduct["breath"] && selectedProduct["breath"].split(".").join("")) + (selectedProduct["height"] && selectedProduct["height"].split(".").join("")) + selectedProduct["modal"] + selectedProduct["type"];
        }

        if (['item_price', 'item_quantity'].includes(fieldName)) {
            errors.note = "";
        }

        this.setState({ selectedProduct, errors, stockData, stock }, () => {
            console.log(this.state);
            // this.checkValidity();
        });
    }

    handleExtraSpecChange = (event, id) => {
        let { selectedProduct } = this.state;

        if (event.target.name === "extra_specifications") {
            if (!selectedProduct["extra_specifications"].includes(id)) {
                selectedProduct["extra_specifications"] = selectedProduct["extra_specifications"].length === 0 ? id : selectedProduct["extra_specifications"] + "," + id;
                selectedProduct["extra_specifications"] = selectedProduct["extra_specifications"].split(",").sort().join(",");
            } else {
                let arr = selectedProduct["extra_specifications"].split(",");
                let newArr = [];
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i] !== id) {
                        newArr.push(arr[i]);
                    }
                }
                selectedProduct["extra_specifications"] = newArr.sort().join(",");
            }
        }
        this.setState({ selectedProduct }, () => {
            // this.checkValidity();
        });
    }
    getExtraSpecifications = () => {
        let { selectedProduct } = this.state;
        return (
            <div className="extra-spec-container">
                <div className="label"><b>Any Specifications:</b></div>
                <div className="extra-spec-input-container">
                    <FormControlLabel
                        control={
                            <Checkbox
                                name="extra_specifications"
                                onChange={(event) => this.handleExtraSpecChange(event, "2")}
                                checked={selectedProduct.extra_specifications.includes("2")}
                                value={2}
                                color="primary"
                            />
                        }
                        label="Grey Cover"
                    />

                    <FormControlLabel
                        control={
                            <Checkbox
                                name="extra_specifications"
                                onChange={(event) => this.handleExtraSpecChange(event, "4")}
                                checked={selectedProduct.extra_specifications.includes("4")}
                                value={4}
                                color="primary"
                            />
                        }
                        label="White Cover"
                    />

                    {/* <FormControlLabel
                        control={
                            <Checkbox
                                name="extra_specifications"
                                onChange={(event) => this.handleExtraSpecChange(event, "1")}
                                checked={selectedProduct.extra_specifications.includes("1")}
                                value={1}
                                color="primary"
                            />
                        }
                        label="Corner Cut / Round Cut"
                    /> */}

                    <FormControlLabel
                        control={
                            <Checkbox
                                name="extra_specifications"
                                onChange={(event) => this.handleExtraSpecChange(event, "3")}
                                checked={selectedProduct.extra_specifications.includes("3")}
                                value={3}
                                color="primary"
                            />
                        }
                        label="Special"
                    />
                </div>
            </div>
        );
    }

    closePopup = () => {
        this.setState({ selectedProduct: {}, enableSubmit: false },
            () => this.props.toggleAddItem(false)
        );
    }

    render() {
        let { selectedProduct, errors } = this.state;
        let { editingIndex } = this.props;
        return (
            <Dialog className="add-product-popup" open={this.props.showAddItem} onClose={this.closePopup} aria-labelledby="form-dialog-title">
                <div className="add-product-title">
                    <div>{editingIndex !== -1 ? "Edit" : "Add"} Products</div>
                    <IconButton onClick={this.closePopup} style={{ float: "right", color: "#ff0000c4" }}>
                        <Close />
                    </IconButton>
                </div>

                {errors.note && <CustomAlert type={"error"} message={errors.note} toggle={true} callbackOnClose={() => this.setState({ errors: { ...errors, note: "" } })} />}

                <div className="add-item-container">
                    <div className="field-container">
                        <div className="label">Select Product:</div>
                        <select
                            name="product"
                            className="custom-dropdown"
                            value={selectedProduct.product}
                            onChange={this.handleChange}
                        >
                            <option value="">Select Product</option>
                            {productDetails.map((product) =>
                                <option key={product.sku} value={product.sku}>{product.label}</option>)}
                        </select>
                        {errors.product && <div className="error-text">{errors.product}</div>}
                    </div>

                    {selectedProduct["supportedDimensions"] && selectedProduct["supportedDimensions"].length > 0 &&
                        <div className="field-container">
                            <div className="label">Select Dimensions:</div>
                            <select
                                name="dimension"
                                className="custom-dropdown"
                                value={selectedProduct.dimension}
                                onChange={this.handleChange}
                            >
                                <option value="">Select Dimension</option>
                                {selectedProduct["supportedDimensions"].map((item) => {
                                    return <option key={item.dimension} value={item.dimension}>{item.label}</option>
                                })}
                            </select>
                            {errors.dimension && <div className="error-text">{errors.dimension}</div>}
                        </div>
                    }

                    {(
                        (selectedProduct["supportedDimensions"] && selectedProduct["supportedDimensions"].length > 0 &&
                            selectedProduct["dimension"]) || (productsHas3Dimensions.includes(selectedProduct.product) || productsHas2Dimensions.includes(selectedProduct.product))
                    ) &&
                        <div className="field-container">
                            <div className="label">Customize Dimensions:</div>
                            <div className="dimension-input-container">
                                <div className="input-field-container">
                                    <input name="length" type="text" className="input-field" onChange={this.handleChange} value={selectedProduct.length} placeholder="L" disabled={disableCustomization.includes(selectedProduct["product"])} />
                                    {errors.length && <div className="error-text">{errors.length}</div>}
                                </div>
                                <div className="input-field-container">
                                    <input name="breath" type="text" className="input-field" onChange={this.handleChange} value={selectedProduct.breath} placeholder="B" disabled={disableCustomization.includes(selectedProduct["product"])} />
                                    {errors.breath && <div className="error-text">{errors.breath}</div>}
                                </div>
                                {productsHas3Dimensions.includes(selectedProduct.product) &&
                                    <div className="input-field-container">
                                        <input name="height" type="text" className="input-field" onChange={this.handleChange} value={selectedProduct.height} placeholder="H" disabled={disableCustomization.includes(selectedProduct["product"])} />
                                        {errors.height && <div className="error-text">{errors.height}</div>}
                                    </div>}
                            </div>
                        </div>
                    }

                    {selectedProduct["product"] === "WSP" && availableTypes[selectedProduct["product"]] &&
                        <div className="field-container">
                            <div className="label">Type:</div>
                            <select
                                name="type"
                                className="custom-dropdown"
                                value={selectedProduct.type}
                                onChange={this.handleChange}
                            >
                                <option value="">Select Type</option>
                                {availableTypes[selectedProduct["product"]].map((item) =>
                                    <option key={`type-${item.key}`} value={item.key}>{item.label}</option>)}
                            </select>
                            {errors.type && <div className="error-text">{errors.type}</div>}
                        </div>
                    }

                    {["WFB", "WSMRC"].includes(selectedProduct["product"]) && availableModels[selectedProduct["product"]] &&
                        <div className="field-container">
                            <div className="label">Modal:</div>
                            <select
                                name="modal"
                                className="custom-dropdown"
                                value={selectedProduct.modal}
                                onChange={this.handleChange}
                            >
                                <option value="">Select Modal</option>
                                {availableModels[selectedProduct["product"]].map((item) =>
                                    <option key={`modal-${item.key}`} value={item.key}>{item.label}</option>)}
                            </select>
                            {errors.modal && <div className="error-text">{errors.modal}</div>}
                        </div>
                    }

                    {["WMP", "WFOCOVER"].includes(selectedProduct["product"]) && availableColors[selectedProduct["product"]] &&
                        <div className="field-container">
                            <div className="label">Color:</div>
                            <select
                                name="modal"
                                className="custom-dropdown"
                                value={selectedProduct.modal}
                                onChange={this.handleChange}
                            >
                                {availableColors[selectedProduct["product"]].map((item) =>
                                    <option key={`modal-${item.key}`} value={item.key}>{item.label}</option>)}
                            </select>
                            {errors.modal && <div className="error-text">{errors.modal}</div>}
                        </div>
                    }

                    {selectedProduct["product"] === "WSMRC" &&
                        <div className="field-container">
                            <div className="label">Type:</div>
                            <select
                                name="type"
                                className="custom-dropdown"
                                value={selectedProduct.type}
                                onChange={this.handleChange}
                            >
                                <option value="">Select</option>
                                <option value="S">Single</option>
                                <option value="D">Double</option>
                            </select>
                            {errors.type && <div className="error-text">{errors.type}</div>}
                        </div>
                    }

                    {selectedProduct["product"] === "WFT" && availableTypes[selectedProduct["product"]] &&
                        <div className="field-container">
                            <div className="label">Modal:</div>
                            <select
                                name="modal"
                                className="custom-dropdown"
                                value={selectedProduct.modal}
                                onChange={this.handleChange}
                            >
                                <option value="">Select Modal</option>
                                {availableTypes[selectedProduct["product"]].map((item) =>
                                    <option key={`modal-${item.key}`} value={item.key}>{item.label}</option>)}
                            </select>
                            {errors.modal && <div className="error-text">{errors.modal}</div>}
                        </div>
                    }

                    {["WDCM", "WOMFM", "WHRFM"].includes(selectedProduct["product"]) && this.getExtraSpecifications()}

                    {this.state.stockData === 'valid' && this.state.stock && <div>
                        <div><b className="label">Stock:</b> {this.state.stock.stock} items</div>
                        {/* <div><b className="label">MRP:</b> {this.state.stock.item_price.mrp}</div> */}
                        {/* <div><b className="label">Price:</b> {this.state.stock.item_price.price} (per Unit)</div> */}
                    </div>}

                    {this.state.stockData === 'valid' && <div className="quantity-price-container">
                        <div className="field-container">
                            <div className="label">Quantity:</div>
                            {this.props.userDetails && this.props.userDetails.role && this.props.userDetails.role.includes('PARTNERSHIPS')
                                ? <input
                                    name="item_quantity"
                                    className="input-field"
                                    value={selectedProduct.item_quantity}
                                    onChange={this.handleChange}
                                    placeholder="Quantity"
                                />
                                : <select
                                    name="item_quantity"
                                    className="custom-dropdown"
                                    value={selectedProduct.item_quantity}
                                    onChange={this.handleChange}
                                >
                                    <option value="">Select</option>
                                    {this.getQuantityOptions()}
                                </select>
                            }
                            {errors.item_quantity && <div className="error-text">{errors.item_quantity}</div>}
                        </div>

                        <div className="field-container">
                            <div className="label">Price(per Unit):</div>
                            <input
                                name="item_price"
                                className="input-field"
                                value={selectedProduct.item_price}
                                // disabled={selectedProduct.item_price}
                                onChange={this.handleChange}
                                placeholder="Price"
                            />
                            {errors.item_price && <div className="error-text">{errors.item_price}</div>}
                        </div>
                    </div>}
                </div>

                <div className="add-product-footer">
                    {this.state.stockData !== 'loading' ? <Button
                        label={this.state.stockData === "notvalid" ?
                            <div>Verify</div> :
                            this.state.stockData === 'valid' && <div><Add className="" />{editingIndex !== -1 ? "Modify" : "Add"}</div>
                        }
                        onClick={this.checkValidity}
                    /> : <CircularProgress />}
                </div>
            </Dialog >
        );
    }
}

AddItem.defaultProps = {
    // productDetails: [],
    selectedProduct: {}
}

AddItem.propTypes = {
    productDetails: PropTypes.object.isRequired,
    getProductDetailsAction: PropTypes.func.isRequired,
    selectedProduct: PropTypes.object,
    userDetails: PropTypes.object
}

const mapStateToProps = (state, ownProps) => {
    return {
        productDetails: state.placeNewOrder.productDetails,
        productData: state.placeNewOrder.productData,
        productError: state.placeNewOrder.productError,
        userDetails: state.userDetails
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ getProductDetailsAction, resetPlaceNewOrderSuccessAndError }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(AddItem);
